import React from 'react';
import './App.css';
import BuildingList from '../BuildingList/BuildingList';
import SearchBar from '../SearchBar/SearchBar';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {imageSrc: '',
                  address: '',
                  area: '',
                  floors: ''};
    this.changeAddress = this.changeAddress.bind(this);
  }

  changeAddress(newAddress,newArea,newImageSrc,newFloors) {
    this.setState({address: newAddress})
    this.setState({area:newArea})
    this.setState({imageSrc: newImageSrc})
    this.setState({floors: newFloors})
  } 

  

  render() {
    return (
      <div className="App">
        <h1>Bygningspas</h1>
        <SearchBar address={this.state.address} onChange={this.changeAddress}/>
        <BuildingList buildings={this.state}/>
      </div>
    );
  }
}

export default App;